"use client";

import React, { useState } from "react";

export default function AnalyzePage() {
    const [contentUrl, setContentUrl] = useState("");
    const [analysisResult, setAnalysisResult] = useState(null);
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState(null); // Added error state

    const handleAnalyze = async () => {
        setLoading(true);
        setAnalysisResult(null);
        setError(null); // Reset error state before new analysis

        try {
            if (!contentUrl) {
                throw new Error("Please enter a video URL");
            }

            // Step 1: Transcribe the video using the API
            console.log('Starting transcription request...');
            const transcriptResponse = await fetch("/api/transcribe", {
                method: "POST",
                headers: { "Content-Type": "application/json" },
                body: JSON.stringify({ videoUrl: contentUrl }),
            });

            if (!transcriptResponse.ok) {
                const errorData = await transcriptResponse.json();
                throw new Error(errorData.error || "Failed to transcribe the video");
            }

            const { transcript } = await transcriptResponse.json();
            console.log('Transcription completed:', transcript?.substring(0, 100) + '...');

            // Step 2: Send transcript to the AI model for analysis
            console.log('Starting analysis...');
            const analysisResponse = await fetch("/api/analyze", {
                method: "POST",
                headers: { "Content-Type": "application/json" },
                body: JSON.stringify({ transcript }),
            });

            if (!analysisResponse.ok) {
                const errorData = await analysisResponse.json();
                throw new Error(errorData.error || "Failed to analyze the transcript");
            }

            const analysisData = await analysisResponse.json();
            console.log('Analysis completed:', analysisData);

            setAnalysisResult(analysisData);
        } catch (error) {
            console.error("Error during analysis:", error);
            setError(error.message);
        } finally {
            setLoading(false);
        }
    };

    return (
        <div className="min-h-screen bg-gray-100 py-12 px-4 sm:px-6 lg:px-8">
            <div className="max-w-3xl mx-auto">
                <div className="bg-white shadow-md rounded-lg p-8">
                    <h1 className="text-3xl font-bold text-center text-gray-900 mb-8">
                        Content Analysis
                    </h1>

                    <div className="mb-6">
                        <label
                            htmlFor="content-url"
                            className="block text-sm font-medium text-gray-700 mb-2"
                        >
                            Enter Content URL or Link
                        </label>
                        <input
                            type="text"
                            id="content-url"
                            value={contentUrl}
                            onChange={(e) => setContentUrl(e.target.value)}
                            placeholder="https://example.com/video-or-article"
                            className="w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-2 focus:ring-blue-500"
                        />
                    </div>

                    {error && (
                        <div className="mb-4 p-4 text-red-700 bg-red-100 rounded-md">
                            {error}
                        </div>
                    )}

                    <div className="flex justify-center">
                        <button
                            onClick={handleAnalyze}
                            disabled={loading}
                            className={`bg-blue-600 text-white px-6 py-3 rounded-md hover:bg-blue-700 transition-colors ${loading ? "opacity-50 cursor-not-allowed" : ""
                                }`}
                        >
                            {loading ? "Analyzing..." : "Analyze Content"}
                        </button>
                    </div>

                    {analysisResult && (
                        <div className="mt-8 bg-gray-50 p-6 rounded-lg">
                            <h2 className="text-xl font-semibold mb-4 text-gray-900">
                                Analysis Results
                            </h2>
                            <div className="space-y-4">
                                {analysisResult.lines?.map((line, index) => (
                                    <div key={index} className="border-b pb-4">
                                        <p className="text-sm font-medium text-gray-600">
                                            Line {index + 1}:
                                        </p>
                                        <p className={`text-lg font-bold ${line.isFake ? "text-red-600" : "text-green-600"
                                            }`}>
                                            {line.isFake ? "False Information" : "True Information"}
                                        </p>
                                        <p className="text-sm text-gray-600">{line.text}</p>
                                    </div>
                                ))}
                            </div>
                        </div>
                    )}
                </div>
            </div>
        </div>
    );
}